body {
  --color-gay: #00acdc;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

ul {
  margin: 0;
  padding: 0;
}

.header-container {
  border-bottom: 0 solid #0000;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  font-size: 12px;
  display: flex;
}

.nav-list {
  justify-content: space-between;
  list-style: none;
  display: flex;
}

.nav-item {
  text-align: center;
}

.nav-link {
  color: #000;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav-link:hover {
  color: var(--color-gay);
}

.nav-item:not(:first-child) > .nav-link {
  margin-left: 20px;
}

.is-active {
  color: var(--color-gay);
}

.is-active:after {
  content: "";
  background-color: var(--color-gay);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0%;
  left: 0;
}
